.body{
    width: 85%;
    height: 80%;

    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.title{
    position: absolute;
    color: #333333;
    left: 4%;
    top: 0px;
    font-weight: 700;
}

.title span{
    font-size: 52px;
}
.product-image{
  position: absolute;
  bottom: -0.2%;
  left: -0.1%;
  padding: 0.3%;
  width: 30%;
  height: 70%;
  z-index: 5;
  border-radius: 0px 200px 150px 0px;
  border-right: 20px solid rgb(202, 202, 202);
}

.search-box{
    position: absolute;
    right: 20%;
    top: 20%;
    width: 30%;
    height: 65px;
    border-radius: 50px;
    z-index: 3;
}
.search-extension{
    position: absolute;
    right: 20%;
    top: 20%;
    width: 30%;
    min-height: 0px;
    border-radius: 30px;
    padding-top: 40px;
    z-index: 2;
}

.suggestion-list{
    color: #fff;
    list-style: none;
    font-size: 22px;
   
}

.suggestion-list li{
    cursor: pointer;
    display: flex;
    flex-direction: row;
}

.suggestion-list li div{
    margin-top: 3px;
    margin-right: 10px;
}
#list-line{
    padding-bottom: 5px;
    margin-top: 5px;
    border-top: 0.01em solid rgb(255, 255, 255, 0.2);
    width: 90%;
}


.search-box input{
    position: absolute;
    top: 50%;
    left: 6%;
    padding-left: 10px;
    font-size: 22px;
    height: 70%;
    width: 70%;
    transform: translate(0%, -50%);
    border: none;
    outline: none;
    
}

::placeholder {
    color: white;
    opacity: 0.7; /* Firefox */
  }
  
.search-icon{
    position: absolute;
    right: 10%;
    top: 50%;
    transform: translate(0%, -50%);
}  

.body-text{
    position: absolute;
    font-weight: 600;
    right: 10%;
    top: 40%;
    width: 50%;
}

.fact-text{
    position: absolute;
    font-weight: 600;
    font-size: 10px;
    right: 15%;
    bottom: 2%;
    width: 50%;
}

.circle-one{
    position: absolute;
    opacity: 0.2;
    top: 5%;
    right: 10%;
    height: 45%;
    width: 20%;
    border-radius: 50%;
  
  }

.circle-two{
    position: absolute;
    opacity: 0.2;
    top: 50%;
    left: 25%;
    height: 45%;
    width: 20%;
    border-radius: 50%;
  
  }

  .circle-three{
    position: absolute;
    opacity: 0.2;
    top: -10%;
    left: -10%;
    height: 45%;
    width: 20%;
    border-radius: 50%;
  
  }
  
  
  
@media (max-width:1300px) {
    .circle-one{
      opacity: 0;
      display: none; 
    }
    .circle-two{
        opacity: 0;
        display: none; 
      }
      .search-box{
        width: 35%;
    } 
    .search-extension{
        width: 35%;
    }
  }
  
  @media (max-width: 1000px) {
 
    .search-box{
        width: 40%;
    }
    .search-extension{
        width: 40%;
    }
  }

  @media (max-width: 900px) {
 
    .search-box{
        width: 60%;
        left: 50%;
        transform: translate(-50%);
    }
    .search-extension{
        width: 60%;
        left: 50%;
        transform: translate(-50%);
    }
    .product-image{
        display: none;
    }
    .title{
        left: 50%;
        transform: translate(-50%);
        top: -16%;
    }
    .body-text{
        width: 70%;
        left: 50%;
        transform: translate(-50%);
    }
    .fact-text{
        width: 70%;
        left: 50%;
        transform: translate(-50%);
    }
  }

  @media (max-width: 500px) {
 
    .search-box{
        width: 80%;
    }
    .search-extension{
        width: 80%;
    }
    .body{
        width: 100%;
    }
    .title{
        font-weight: 600;
        top: 60px;
    }
    .title span{
        font-size: 34px;
    }
    .body-text{
        width: 90%;
        font-size: 13px;
    }
    .fact-text{
        width: 90%;
    }

    .search-box{
        position: absolute;
        top: 10px;
        width: 100%;
    }
    .search-box input::placeholder{
        opacity: 0;
    }
    .search-extension{
        border-radius: 0;
        min-height: 1000px;
        top: 10px;
        width: 100%;
        border-radius: 0px 0px 0px 50px;
    }
    .circle-three{
        width: 260px;
    }
 
  }

  @media (max-width: 240px) {
 
    .search-extension{
       
        border-radius: 0px;
    }
    .body{
        width: 100%;
    }
    .title{
        font-size: 16px;
        font-weight: 600;
    }
    .title span{
        font-size: 26px;
    }
    .body-text{
        width: 90%;
        font-size: 11px;
    }
    .fact-text{
        width: 90%;
        font-size: 9px;
    }
    .circle-three{
        display: none;
    }
 
  }