
.extension{
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    z-index: 4;
    
    background-color: rgba(0, 0, 0, 0.1);
    line-height: 1;
   -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(2px);
}

.extension-box{
    position: absolute;
    top: 0%;
    width: 36%;
    height: 100%;
    border-radius: 0px 50px 50px 0px;
    z-index: 5;
    transition: 0.3s ease;

    border-right:  15px solid #5E8F7E;
    border-left:  15px solid #5E8F7E;
}

.extend{
    width: 0%;  
    opacity: 0;
 }

 .extension-main{
   position: absolute;
   width: 90%;
   left: 10%;
   height: 90%;
   top: 0%;
   
 }

.extension-body{
  overflow-y: scroll;
  width: 100%;
  max-height: 80%;
  padding-bottom: 1%;
}

/* width */
::-webkit-scrollbar {
  width: 15px;
  margin-left: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  background: none;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #555555;
  border-radius: 20px 0px 0px 20px;
}

#name{
  position: relative;
  top: -25px;

  height: 1%;
}

 .extension-main h1{
   font-size: 56px;
   font-weight: 500;
 }

 .extension-body h2{
   font-size: 18px;
 }
 .extension-body p{
  font-size: 13px;
  color: #5A5757;
}

.extension-child{

  display: flex;
  flex-direction: row;
  margin-left: 15px;
  width: 80%;

}

#line{
  border-left: 2px solid #555555;
  min-height: 0px;
  margin-right: 10px;
}

#horizontal-line{
  border-top: 2px solid #555555;
  width: 100px;
  margin-left: 15px;

  font-size: 14px;
  color: #5A5757;
  font-weight: 500;
}


 .btn-group{
   width: 100%;
   position: absolute;
   bottom: 2%;

   display: flex;
   justify-content: space-evenly;
   left: -4%;
 }
 .btn-group button{
  
   width: 20%;
   padding: 1.5%;
   border-radius: 50px;
   box-shadow: 0px 5px #e0e0e0;
   
 }

 .btn-group button:active{
   opacity: 0.7;
   box-shadow: 0px 2px #9c9c9c;
 }

 .btn-group button:first-child{
  border: none; 
 }

 .btn-group button:nth-child(2){
  background: none; 
 }


 @media (max-width: 1600px) {
  .extension-body{
    max-height: 75%;
  } 
}

 
 @media (max-width: 1400px) {
 
    .extension-box{
        width: 40%;
    }
  }

  @media (max-width: 1100px) {
 
    .extension-box{
        width: 60%;
        border-radius: 40px;
        left: 50%;
        transform: translate(-50%);
    }

    .extension-body{
      max-height: 70%;
    }
    
  }  

  
  @media (max-width: 900px) {
 
    .extension-box{
        width: 70%;
       
    }
    
  }  

    
  @media (max-width: 700px) {
 
    .extension-box{
        width: 80%;
      
    }
    
  }  
      
  @media (max-width: 500px) {
 
    .extension-box{
        border-radius: 0px;
        width: 100%;
        height: 100%;
    }
    
  }  